<template>
  <div class='popup-add-shop-cart'>
    <van-popup v-bind="$attrs" v-on="$listeners" v-model="isShow" position="bottom">
      <div class="food">
        <div class="food-img">
          <img src="static/images/category/jz.png"/>
        </div>
        <div class="food-detail">
          <div class="food-detail-top food-name vertical-center">
            <span>澳大利亚橙子&nbsp;绿色食品</span>
          </div>
          <div class="food-detail-bottom">
            <div class="food-specification">
              <span>规格:&nbsp;可任选3件</span>
            </div>
            <div class="food-price">
              <span>￥: 30.9</span>
            </div>
          </div>
        </div>
      </div>
      <div class="selecy-specification">
        <div :class="{'specification-item': true, 'specification-item-active': item.isSel}"
            v-for="(item ,index) in productInfo.specification" :key="index"
            @click="clickPecificationItem(item)">
          <span>{{item.name}}&nbsp;{{item.weight}}kg</span>
          <img :src="item.isSel ? 'static/images/commonicon/xz1.png' : 'static/images/commonicon/wxz1.png'"/>
        </div>
      </div>
      <div class="buy-number">
        <div>
          <span>件数</span>
        </div>
        <div>
          <van-stepper v-model="buyNumber" min="1"/>
        </div>
      </div>
      <div class="button-add-shop-cart">
        <van-button @click="addShopCart()" type="default">加入菜篮子</van-button>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'PopupAddShopCart',
  components: {},
  inheritAttrs: false,
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false
    }
  },
  data () {
    return {
      isShow: false,
      // 商品信息
      productInfo: {
        // 规格
        specification: [
          { name: '澳大利亚橙子', weight: 1, isSel: false }, { name: '澳大利亚橙子', weight: 2, isSel: false }, { name: '澳大利亚橙子', weight: 5, isSel: false }
        ]
      },
      buyNumber: 1
    }
  },
  computed: {},
  created () {
  },
  methods: {
    addShopCart () {
      var id = 1
      this.$router.push({path: '/shoppingcart', query: { id: id }})
    },
    clickPecificationItem (curItem) {
      if (curItem.isSel) {
        curItem.isSel = false
      } else {
        this.productInfo.specification = this.productInfo.specification.map(item => {
          item.isSel = false
          return item
        })
        curItem.isSel = true
      }
    }
  },
  watch: {
    show (val) {
      this.isShow = val
    },
    isShow (val) {
      if (!val) {
        this.$emit('update:show', false)
      }
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .popup-add-shop-cart {
    font-family: $font-family-default!important;
    line-height: 1.5;
    .van-popup{
      padding: 10px 30px 0 30px;
      .food{
        height: 150px;
        display: flex;
        .food-img{
          width: 150px;
          height: 150px;
          img{ width:150px;height: 100% }
        }
        .food-detail{
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          width: calc(100% - 210px);
          height: 100%;
          padding-left: 15px;
          font-family: $font-family-default;
          .food-detail-top{
            height: calc(100% - 68.86px);
          }
          .food-name{
            font-size: 29px;
            font-weight: 700;
          }
          .food-detail-bottom{
            height: 68.86px;
            .food-specification{
              font-size: 24px;
              color: $black999999;
            }
            .food-price{
              font-size: 24px;
              color: $redffd92d16;
            }
          }
        }
      }
      .selecy-specification{
        margin-top: 20px;
        padding: 20px 20px;
        .specification-item+.specification-item{
          margin-top: 40px;
        }
        .specification-item{
          line-height: 1;
          height: 40px;
          font-size: 30px;
          display: flex;
          justify-content: space-between;align-items: center;
          img{
            width: 40px;height: 40px;
          }
        }
        .specification-item-active{
          color: $green22ac38;
        }
      }
      .buy-number{
        margin-top: 100px;
        font-size: 28px;
        padding: 0 20px;
        /deep/ .van-stepper1{
          button{
            height: 100px!important;
          }
          input{
            height: 100px!important;
          }
        }
      }
      .button-add-shop-cart{
        margin: 40px 0 60px 0;
        /deep/ .van-button{
          line-height: 1.5;
          background-color: $green22ac38;
          color: $whiteffffff;
          height: 76px;
          width: 100%;
          border-radius: 8px;
          font-weight: 600;
          font-family: $font-family-default;
        }
      }
    }
  }
</style>



