<template>
  <div class="content" >
  <van-dialog
    v-model="isShow"
    :before-close="beforeClose"
    class="container1"
    confirm-button-text="立刻填写"
  >
    <div class="container">
      <div class="top-box">
        <div class="img-box"><img src="static/images/category/inputinfo/txgrxx.png" class="img"></div>
        <div class="title-box"><span>填写个人信息</span></div>
      </div>
      <div class="middle-box">
        <div class="title-box1"><img src="static/images/commonicon/grxz.png" class="img1"><span class="title">{{title}}</span></div>
        <div class="title-box1"><img src="static/images/commonicon/grxz.png" class="img1"><span class="title">{{title}}</span></div>
        <div class="title-box1"><img src="static/images/commonicon/grxz.png" class="img1"><span class="title">{{title}}</span></div>
      </div>
    </div>
    <!--<div class="img-box1" v-if="this.isShow">-->
    <!--&lt;!&ndash;<img src="static/images/commonicon/gb.png" class="img1" @click="close">&ndash;&gt;-->
    <!--<div class="icon-box"><i class="icon_false"></i></div>-->
  <!--</div>-->
    <div class="img-box1" v-if="this.isShow">
      <!--<img src="static/images/commonicon/gb.png" class="img1" @click="close">-->
      <div class="icon-box" @click="close"><i class="icon_false"></i></div>
    </div>
  </van-dialog>

  </div>
</template>

<script>
export default {
  name: 'Popup-Dialog',
  props: {
    show: {
      type: Boolean,
      default: false,
      require: true
    },
    title: {
      type: String,
      default: '',
      require: true
    }
  },
  data () {
    return {
      isShow: false
    }
  },

  methods: {
    beforeClose (action, done) {
      if (action === 'confirm') {
        done()
        this.go()
      } else {
        done()
      }
    },
    go () {
      this.$router.push({path: '/personalcenter/updPersonalData/addPersonalData'})
    },
    close () {
      this.isShow = false
    }
  },
  watch: {
    show (val) {
      this.isShow = val
    },
    isShow (val) {
      if (!val) {
        this.$emit('update:show', false)
      }
    }
  }
}
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';

  .content{
    height: 100%;
    width: 100%;
    position: relative;
  .container1{
    width: 580px;
    height: 652px;
    border-radius: 20px;
    /deep/ .van-dialog__content{
      width: 100%;
      height: calc(100% - 110px);
      .container{
        height: 100%;
        .top-box{
          height: 50%;
          .img-box{
            height: 80%;
            display: flex;
            justify-content: center;
            align-items: center;
            .img{
              width: 138px;
              height: 138px;
            }
          }
          .title-box{
            height: 20%;
            text-align: center;
            font-family: PingFang-SC-Bold;
            font-size: 30px;
            font-weight: normal;
            font-stretch: normal;
            line-height: 34px;
            letter-spacing: 0px;
            color: $black333333;
          }
        }
        .middle-box{
          height: 50%;
          .title-box1{
            height: 20%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            .img1{
              width: 40px;
              height: 40px;
              margin-left: 20px;
            }
            .title{
              margin-left:20px;
              font-family: $font-family-Medium;
              font-size: 26px;
              font-weight: normal;
              font-stretch: normal;
              line-height: 34px;
              letter-spacing: 0px;
              color: $black666666;
            }
          }
        }
      }
    }
  }
    .img-box1{
      position: absolute;
      z-index: 9999;
      top:-190px;
      right: 0px;
      .icon-box {
        width: 48px;
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;
        /*border: 2px solid gray;*/
        background-color: white;
        border-radius: 40px;
        position: absolute;
        top: 200px;
        right: 20px;
        .icon_false {
          display: inline-block;
          width: 36px;
          height: 2px;
          background:gray;
          line-height: 0;
          font-size: 0;
          vertical-align: middle;
          transform: rotate(45deg);
        }

        .icon_false:after {
          content: '/';
          display: block;
          width: 36px;
          height: 2px;
          background:gray;
            transform: rotate(-90deg);
        }

      }
    }
  /deep/ .van-dialog__footer{
    height: 110px;
    background: $green22ac38;
    .van-button{
      background: $green22ac38;
      font-family: PingFang-SC-Medium;
      font-size: 30px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 34px;
      letter-spacing: 0px;
      color: $whiteffffff;
    }
  }
  }
</style>



