<template>
  <div class="app-page">
    <page-content class="page-content">
      <div class="container">
        <div class="product-img">
          <img class="icon" v-for="img in imageList" v-lazy="img" :key="img.id">
        </div>
        <div class="product-detail" v-for="(item, index) of list" :key="index">
          <div class="product-name">{{item.name}}</div>
          <div class="product-title">{{item.title}}</div>
          <div class="product-explan">{{item.explan}}</div>
          <div class="price">
            <span class="product-price">￥{{item.sufixprice}}<small>{{item.fixprice}}</small></span>
            <span class="sell">{{item.sell}}</span>
            <span class="unsell">{{item.unsell}}</span>
          </div>
          <div class="tips">{{item.tips}}</div>
        </div>
        <div class="product-info">
          <div class="item">
            <div class="flex-left">规格</div><div class="flex-right">1件</div>
          </div>
          <div class="item">
          <div class="flex-left">包装</div><div class="flex-right">散装</div>
          </div>
          <div class="item">
          <div class="flex-left">重量</div><div class="flex-right">5kg</div>
          </div>
          <div class="item">
          <div class="flex-left">保质期</div><div class="flex-right">请于2018-12-31日前食用</div>
          </div>
          <div class="item">
          <div class="flex-left">储存方法</div><div class="flex-right">常温储存</div>
          </div>
        </div>
        <div class="bottom-comment">
          <TitleSecond title="评论" :hideRight="true" class="title-second1" :size="smallsize"></TitleSecond>
          <div class="comment-detail">
            <div class="detail-header">
              <div class="left-user">
                <div class="user-img">
                  <img src="static/images/personal/logo.png"/>
                  <span>网友一号</span>
                </div>
              </div>
              <div class="right-star">
                <van-rate v-model="value" :size="25" :count="5" color="#5fab36" void-color="#e6e6e6"/>
              </div>
            </div>
            <div class="detail-content"><span>感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评</span></div>
            <div class="detail-img" v-if="isimgcomment">
              <img src="static/images/personal/pg1.png" />
              <img src="static/images/personal/spxx.png" />
              <img src="static/images/personal/yz1.png"/>
            </div>
            <div class="detail-footer"><span>2019-01-25</span></div>
          </div>
          <div class="comment-detail">
            <div class="detail-header">
              <div class="left-user">
                <div class="user-img">
                  <img src="static/images/personal/logo.png"/>
                  <span>网友一号</span>
                </div>
              </div>
              <div class="right-star">
                <van-rate v-model="value" :size="25" :count="5" color="#5fab36" void-color="#e6e6e6"/>
              </div>
            </div>
            <div class="detail-content"><span>感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评感谢您的好评</span></div>
            <div class="detail-img" v-if="!isimgcomment">
              <img src="static/images/personal/pg1.png" />
              <img src="static/images/personal/spxx.png" />
              <img src="static/images/personal/yz1.png"/>
            </div>
            <div class="detail-footer"><span>2019-01-25</span></div>
          </div>
        </div>
      </div>
      <van-goods-action>
        <van-goods-action-big-btn text="加入菜篮子" @click="onClickBigBtn"/>
        <van-goods-action-big-btn primary text="立即购买" @click="onClickBigBtn1"/>
      </van-goods-action>
    </page-content>
    <popupshopcart :show.sync="popup.shopcartshow"></popupshopcart>
    <popupdialog :show.sync="popup.popshow" :title="popup.title"></popupdialog>
  </div>
</template>

<script>
import PageContent from '@/components/page/content'
import popupdialog from '@/views/category/productdetail/popup-dialog'
import popupshopcart from '@/components/Popup/popup-add-shop-cart'
import TitleSecond from '@/components/page/title'
export default {
  name: 'index',
  components: {
    PageContent,
    popupdialog,
    popupshopcart,
    TitleSecond
  },
  data () {
    return {
      imageList: ['static/images/category/product-package/tcdt.png'],
      list: [
        {
          name: '水果套餐 水果套餐',
          title: '商品信息',
          explan: '商品说明商品说明商品说明商品说明商品说明商品说明商品说明商品说明商品说明商品说明',
          sufixprice: '27.',
          fixprice: '8',
          sell: '已售30件',
          unsell: '库存30件',
          tips: '温馨提示: 不支持七天无理由退款'
        }
      ],
      // 弹出层变量
      popup: {
        shopcartshow: false,
        popshow: false,
        title: '填写个人信息'
      },
      smallsize: 'smallsize',
      value: 3,
      isimgcomment: false
    }
  },
  methods: {
    onClickBigBtn () {
      const a = true
      if (a) {
        this.popup.popshow = true
      } else {
        this.$router.push({path: '/shoppingcart', query: { id: 1 }})
      }
    },
    onClickBigBtn1 () {
      this.popup.shopcartshow = true
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "@/styles/variables.scss";
.app-page{
}
.page-content {
  position: relative;
  .container {
    height: 100%;
    /*display: flex;*/
    /*flex-direction: column;*/
    overflow: auto;
    .product-img {
      height: 40%;
      .icon{
        height: 100%;
        width: 100%;
      }
    }
    .product-detail {
      padding: 0 30px 0 30px;
      height: 25%;
      border-bottom: 15px solid $whitef5f5f5;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .product-name{
        font-size: 30px;
        font-weight: bold;
        font-family: $font-family-default;
      }
      .product-title{
        font-size: 24px;
        color: $black999999;
        font-family: $font-family-default;
      }
      .product-explan{
        font-size: 30px;
        color: $black666666;
        font-family: $font-family-default;
        font-weight: normal;
        font-stretch: normal
      }
      .price{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .product-price{
          font-size: 45px;
          color: $redc8251d;
          font-family: $font-family-default;
          small{
            font-size: smaller;
          }
        }
        .sell{
          font-size: 24px;
          color: $black999999;
          font-family: $font-family-default;
        }
        .unsell {
          font-size: 24px;
          color: $black999999;
          font-family: $font-family-default;
        }
      }
      .tips{
        font-size: 24px;
        color: $black999999;
        font-family: $font-family-default;
      }
    }
    .product-info {
      /*flex: 1;*/
      padding: 0 30px 0 30px;
      width: 100%;
      border-bottom: 15px solid $whitef5f5f5;
      height: 35%;
      .item{
        width: 100%;
        display: flex;
        /*height: 12%;*/
        padding: 20px 0 20px 0;
        align-items: center;
      }
      .flex-left {
        width: 15%;
        font-size: 24px;
        color: $black999999;
        font-family: $font-family-default;
      }
      .flex-right{
        width: 85%;
        font-size: 24px;
        font-family: $font-family-default;
      }
    }
    .bottom-comment{
      height: 50%;
      padding: 0 30px 0 30px;
      .title-second1{
        /*padding-left: 30px;*/
      }
      .comment-detail{
        border-bottom: 2px solid $whitef5f5f5;
        .detail-header{
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding: 20px 0px 20px 0px;
          .left-user{
            .user-img{
              img{
                width: 46px;
                height: 46px;
              }
              span{
                margin-left: 5px;
                font-size: 28px;
                color: $black333333;
                font-family: $font-family-Medium;
              }
            }
          }
          .right-star{}
        }
        .detail-content{
          span{
            font-size: 24px;
            color: $black333333;
            font-family: $font-family-Medium;
            padding: 10px 0 10px 0;
          }
        }
        .detail-img{
          display: flex;
          flex-wrap: nowrap;
          justify-content: space-around;
          padding: 10px 0 10px 0;
          img{
            height: 150px;
            width: 150px;
          }
        }
        .detail-footer{
          text-align: right;
          padding: 10px 0 10px 0;
          span{
            font-size: 24px;
            color: $black999999;
            font-family: $font-family-Medium;
          }
        }
      }
    }
  }
 .van-goods-action {
   bottom: 60px;
   padding: 0 30px 0 30px;
 }
  .van-button--warning {
    background-color:$green5fab36;
    border: 2px solid $green5fab36;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  .van-button--danger {
    background-color: $green40672b;
    border: 2px solid $green40672b;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .van-button--large {
    height: 76px;
    line-height: 76px;
    font-family: $font-family-default;
  }
  .page-content .container{
    margin: 0;
  }
}
</style>



